/* Luxury Golden Gradients - محسنة مع تأثيرات توهج أقوى */
.gold-gradient {
    background: linear-gradient(90deg, #f5d97a, #d2b75f, #f5d97a);
    background-size: 200% 100%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    animation: shimmer 3s ease-in-out infinite;
    text-shadow: 0 0 20px rgba(245, 217, 122, 0.6);
  }
.gold-gradient-static {
    background: linear-gradient(135deg, #f5d97a 0%, #e6c36a 25%, #d2b75f 50%, #c9a44d 75%, #a3833e 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 0 15px rgba(245, 217, 122, 0.5);
  }
.luxury-text-shadow {
    text-shadow: 
      0 0 15px rgba(245, 217, 122, 0.5),
      0 0 30px rgba(230, 195, 106, 0.3),
      0 0 45px rgba(210, 183, 95, 0.2),
      0 0 60px rgba(192, 171, 86, 0.1);
  }
/* تأثيرات توهج للخطوط البيضاء */
/* تأثيرات توهج ذهبية محسنة */
.gold-glow {
    text-shadow: 
      0 0 15px rgba(245, 217, 122, 0.8),
      0 0 30px rgba(230, 195, 106, 0.6),
      0 0 45px rgba(210, 183, 95, 0.4),
      0 0 60px rgba(192, 171, 86, 0.2);
  }
.gold-glow-strong {
    text-shadow: 
      0 0 20px rgba(245, 217, 122, 1),
      0 0 40px rgba(230, 195, 106, 0.8),
      0 0 60px rgba(210, 183, 95, 0.6),
      0 0 80px rgba(192, 171, 86, 0.4);
  }
.luxury-glow {
    filter: drop-shadow(0 0 8px rgba(245, 217, 122, 0.4));
  }
.luxury-border {
    border: 1px solid;
    -o-border-image: linear-gradient(90deg, #f5d97a, #d2b75f, #f5d97a) 1;
       border-image: linear-gradient(90deg, #f5d97a, #d2b75f, #f5d97a) 1;
  }
/* Liquid Glass Effect */
.liquid-glass {
    background: linear-gradient(135deg, 
      rgba(255, 255, 255, 0.1) 0%,
      rgba(255, 255, 255, 0.05) 25%,
      rgba(245, 217, 122, 0.1) 50%,
      rgba(255, 255, 255, 0.05) 75%,
      rgba(255, 255, 255, 0.1) 100%
    );
    backdrop-filter: blur(20px);
    border: 1px solid rgba(245, 217, 122, 0.2);
  }
