:root {
  --nav-h: 72px;
  --gold: 255 200 80;
  --gold-rgb: rgb(var(--gold));
}

body {
  padding-top: var(--nav-h);
  background-color: #000;
  color: #fff;
}

[id] {
  scroll-margin-top: var(--nav-h);
}

html { 
  -webkit-font-smoothing: antialiased; 
  -moz-osx-font-smoothing: grayscale;
  /* Performance optimizations */
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
}

/* GPU Acceleration and Performance Optimizations */
* {
  box-sizing: border-box;
}

/* Optimize animations for better performance */
*,
*::before,
*::after {
  will-change: auto;
}

/* Optimize images for better performance */
img {
  content-visibility: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Optimize iframes */
iframe {
  content-visibility: auto;
}

/* Specific performance optimizations for animated elements */
.animate-shimmer,
.animate-gold-pulse,
.animate-ping,
.animate-spin,
.animate-bounce {
  will-change: transform, opacity;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
}

/* Optimize images and media */
/* الصور يجب أن تحتفظ بارتفاع تلقائي، لكن الفيديوهات يجب أن تملأ الحاوية */
img {
  max-width: 100%;
  height: auto;
  backface-visibility: hidden;
}

video {
  max-width: 100%;
  height: 100%; /* يضمن ملء الفيديو للحاوية (الهيرو/البلوك) */
  backface-visibility: hidden;
}

/* Optimize scrolling performance */
.overflow-auto,
.overflow-scroll {
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}

/* Reduce layout thrashing */
.motion-reduce {
  animation-duration: 0.01ms !important;
  animation-iteration-count: 1 !important;
  transition-duration: 0.01ms !important;
  scroll-behavior: auto !important;
}

/* Optimize for mobile devices */
@media (max-width: 768px) {
  * {
    -webkit-tap-highlight-color: transparent;
  }
  
  .animate-shimmer,
  .animate-gold-pulse {
    animation-duration: 2s;
  }
}
[dir="rtl"] body { font-family: var(--font-tajawal), system-ui, -apple-system, Segoe UI, Roboto, Arial, "Apple Color Emoji", "Segoe UI Emoji", sans-serif; }
[dir="ltr"] body { font-family: var(--font-inter), system-ui, -apple-system, Segoe UI, Roboto, Arial, "Apple Color Emoji", "Segoe UI Emoji", sans-serif; }

/* Luxury Typography Hierarchy */
h1, h2, h3, h4, h5, h6 { 
  font-family: var(--font-amiri), var(--font-tajawal), serif; 
  font-weight: 700;
  line-height: 1.2;
}

h1 { 
  font-size: clamp(2.5rem, 5vw, 4rem); 
  font-weight: 700;
  letter-spacing: -0.02em;
}

h2 { 
  font-size: clamp(2rem, 4vw, 3rem); 
  font-weight: 700;
  letter-spacing: -0.01em;
}

h3 { 
  font-size: clamp(1.5rem, 3vw, 2.25rem); 
  font-weight: 600;
}

.luxury-title {
  font-family: var(--font-amiri), var(--font-tajawal), serif;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.luxury-subtitle {
  font-family: var(--font-tajawal), system-ui, sans-serif;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: 0.01em;
}

.luxury-body {
  font-family: var(--font-tajawal), system-ui, sans-serif;
