}

.gold-hover:hover { text-shadow: 0 0 18px rgba(230,195,106,.35); }

@keyframes ping-slow { 0%, 100% { transform: scale(0.8); opacity: 0.2; } 50% { transform: scale(1.2); opacity: 0.6; } }
@keyframes ping-medium { 0%, 100% { transform: scale(0.6); opacity: 0.4; } 50% { transform: scale(1.0); opacity: 0.8; } }
@keyframes ping-fast { 0%, 100% { transform: scale(0.4); opacity: 0.6; } 50% { transform: scale(0.8); opacity: 1; } }

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

@keyframes liquidGold {
  0% { 
    background-position: 0% 50%;
    transform: scale(1) rotate(0deg);
  }
  25% { 
    background-position: 100% 50%;
    transform: scale(1.05) rotate(1deg);
  }
  50% { 
    background-position: 100% 100%;
    transform: scale(1.1) rotate(0deg);
  }
  75% { 
    background-position: 0% 100%;
    transform: scale(1.05) rotate(-1deg);
  }
  100% { 
    background-position: 0% 50%;
    transform: scale(1) rotate(0deg);
  }
}

@keyframes logoGlow {
  0%, 100% { 
    filter: drop-shadow(0 0 20px rgba(245, 217, 122, 0.6));
    transform: scale(1);
  }
  50% { 
    filter: drop-shadow(0 0 40px rgba(245, 217, 122, 0.9));
    transform: scale(1.02);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeOut {
  0% { opacity: 1; }
  100% { opacity: 0; }
}

/* Luxury Map Tooltips */
.luxury-tooltip {
  background: rgba(0, 0, 0, 0.9) !important;
  border: 1px solid rgba(245, 217, 122, 0.3) !important;
  border-radius: 8px !important;
  color: #f5d97a !important;
  font-weight: 600 !important;
  padding: 8px 12px !important;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5), 0 0 15px rgba(245, 217, 122, 0.2) !important;
  backdrop-filter: blur(10px) !important;
}

.luxury-tooltip::before {
  border-top-color: rgba(0, 0, 0, 0.9) !important;
}

.poi-tooltip {
  background: rgba(107, 114, 128, 0.9) !important;
  border: 1px solid rgba(156, 163, 175, 0.3) !important;
  border-radius: 6px !important;
  color: #f3f4f6 !important;
  font-size: 12px !important;
  padding: 6px 10px !important;
}

/* Custom Marker Styles */
.custom-marker {
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.3));
}

.custom-marker:hover {
  transform: scale(1.1);
  filter: drop-shadow(0 6px 12px rgba(245, 217, 122, 0.4));
}

.animate-ping-slow { animation: ping-slow 3s cubic-bezier(0, 0, 0.2, 1) infinite; }
.animate-ping-medium { animation: ping-medium 2s cubic-bezier(0, 0, 0.2, 1) infinite; }
.animate-ping-fast { animation: ping-fast 1s cubic-bezier(0, 0, 0.2, 1) infinite; }

/* Splash Screen Animations */
.liquid-gold-bg {
  background: linear-gradient(45deg, 
    #000000 0%, 
    #1a1a1a 20%, 
    #d6b253 40%, 
    #f5d97a 50%, 
    #d6b253 60%, 
    #1a1a1a 80%, 
    #000000 100%);
  background-size: 400% 400%;
  animation: liquidGold 4s ease-in-out infinite;
}

.logo-glow {
  animation: logoGlow 2s ease-in-out infinite;
}

.fade-in-up {
  animation: fadeInUp 0.8s ease-out forwards;
}

.fade-out {
  animation: fadeOut 0.5s ease-in forwards;
}

