/* Performance Optimizations */
.will-change-transform {
  will-change: transform;
}

.will-change-opacity {
  will-change: opacity;
}

.will-change-auto {
  will-change: auto;
}

/* GPU Acceleration for better performance */
.gpu-accelerated {
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
}

/* Enhanced Liquid Gold Background with better performance */
@keyframes liquidGold {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.liquid-gold-bg {
  background: linear-gradient(45deg, 
    #000000 0%, 
    #1a1a1a 20%, 
    #d6b253 40%, 
    #f5d97a 50%, 
    #d6b253 60%, 
    #1a1a1a 80%, 
    #000000 100%);
  background-size: 400% 400%;
  animation: liquidGold 4s ease-in-out infinite;
  will-change: background-position;
}

/* Optimized Float Animations */
@keyframes float-slow {
  0%, 100% { 
    transform: translateY(0px) translateX(0px) rotate(0deg);
    opacity: 0.4;
  }
  25% { 
    transform: translateY(-20px) translateX(10px) rotate(90deg);
    opacity: 0.6;
  }
  50% { 
    transform: translateY(-40px) translateX(-5px) rotate(180deg);
    opacity: 0.8;
  }
  75% { 
    transform: translateY(-20px) translateX(-15px) rotate(270deg);
    opacity: 0.6;
  }
}

@keyframes float-medium {
  0%, 100% { 
    transform: translateY(0px) translateX(0px) rotate(0deg);
    opacity: 0.5;
  }
  33% { 
    transform: translateY(-15px) translateX(15px) rotate(120deg);
    opacity: 0.7;
  }
  66% { 
    transform: translateY(-30px) translateX(-10px) rotate(240deg);
    opacity: 0.9;
  }
}

@keyframes float-fast {
  0%, 100% { 
    transform: translateY(0px) translateX(0px) rotate(0deg);
    opacity: 0.3;
  }
  50% { 
    transform: translateY(-25px) translateX(20px) rotate(180deg);
    opacity: 0.7;
  }
}

/* Enhanced Shimmer Effects */
@keyframes shimmer-wave {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes shimmer-wave-reverse {
  0% { transform: translateX(100%); }
  100% { transform: translateX(-100%); }
}

@keyframes shimmer-text {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

/* Enhanced Pulse Effects */
@keyframes pulse-gold {
  0%, 100% { 
    filter: drop-shadow(0 0 20px rgba(245, 217, 122, 0.6));
    transform: scale(1);
  }
  50% { 
    filter: drop-shadow(0 0 40px rgba(245, 217, 122, 0.9));
    transform: scale(1.05);
  }
}

@keyframes fade-in-up {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes loading-bar {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes pulse-slow {
  0%, 100% { opacity: 0.3; }
  50% { opacity: 0.6; }
}

/* Optimized Animation Classes with GPU acceleration */
.animate-float-slow { 
  animation: float-slow 6s ease-in-out infinite;
  will-change: transform, opacity;
}

.animate-float-medium { 
  animation: float-medium 4s ease-in-out infinite;
  will-change: transform, opacity;
}

.animate-float-fast { 
  animation: float-fast 3s ease-in-out infinite;
  will-change: transform, opacity;
}

.animate-shimmer-wave { 
  animation: shimmer-wave 3s ease-in-out infinite;
  will-change: transform;
}

.animate-shimmer-wave-reverse { 
  animation: shimmer-wave-reverse 4s ease-in-out infinite;
  will-change: transform;
}

.animate-shimmer-text { 
  background-size: 200% 100%;
  animation: shimmer-text 2s ease-in-out infinite;
  will-change: background-position;
}

.animate-pulse-gold { 
  animation: pulse-gold 2s ease-in-out infinite;
  will-change: filter, transform;
}

.animate-fade-in-up { 
  animation: fade-in-up 0.8s ease-out forwards;
  will-change: opacity, transform;
}

.animate-fade-in { 
  animation: fade-in 0.7s ease-out forwards;
  will-change: opacity;
}

.animate-loading-bar { 
  animation: loading-bar 2s ease-in-out infinite;
  will-change: transform;
}

.animate-pulse-slow { 
  animation: pulse-slow 4s ease-in-out infinite;
  will-change: opacity;
}

/* Enhanced Luxury Title with better performance */
.luxury-title-splash {
  font-family: var(--font-amiri), serif;
  text-shadow: 
    0 0 20px rgba(245, 217, 122, 0.5),
    0 0 40px rgba(230, 195, 106, 0.3),
    0 0 60px rgba(210, 183, 95, 0.2);
  will-change: background-position;
}

/* Smooth Scrolling Enhancement */
html {
  scroll-behavior: smooth;
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  
  .will-change-transform,
  .will-change-opacity {
    will-change: auto;
  }
}

/* Loading States */
.loading-skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading-skeleton 1.5s infinite;
}

@keyframes loading-skeleton {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Intersection Observer Optimization */
.lazy-load {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.lazy-load.loaded {
  opacity: 1;
  transform: translateY(0);
}

/* Enhanced Button Hover Effects */
.btn-gold {
  background: linear-gradient(135deg, #d4af37 0%, #f5d97a 50%, #d4af37 100%);
  color: #1a1a1a;
  border-radius: 8px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, box-shadow;
}

.btn-gold:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(212, 175, 55, 0.4);
}

/* Gradient Utilities */
.bg-gradient-radial {
  background: radial-gradient(circle, var(--tw-gradient-stops));
}

/* Line clamp utilities for text truncation */
.line-clamp-1 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}

.line-clamp-2 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.line-clamp-3 {
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
}

/* Enhanced performance for carousels */
.carousel-item {
  will-change: transform;
  transform: translateZ(0);
}

/* Optimize iframe loading */
iframe {
  content-visibility: auto;
  will-change: transform;
}
